import { useReducer, useEffect } from 'react';
import { stringify } from 'qs';
import { useFlexgetAPI } from 'core/api';
const historyReducer = (state, { items, total, reset }) => {
    if (reset) {
        return {
            items,
            total,
        };
    }
    return {
        items: [...state.items, ...items],
        total,
    };
};
export const useHistoryPlugin = (options) => {
    const [state, dispatch] = useReducer(historyReducer, { items: [], total: 0 });
    const [requestState, request] = useFlexgetAPI(`/history?${stringify(options)}`);
    useEffect(() => {
        const fn = async () => {
            const resp = await request();
            if (resp.ok) {
                dispatch({
                    items: resp.data,
                    total: parseInt(resp.headers.get('total-count') ?? '0', 10),
                    reset: options.page === 1,
                });
            }
        };
        fn();
    }, [options.page, request]);
    return { ...state, ...requestState };
};
